
/* header */

.header {
    background-color: var(--nav-color);
    box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, .1);
    position: fixed;
    width: 100%;
    z-index: 3;
    color: white;
    text-shadow: var(--text-shadow);
}

.header ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
    background-color: var(--nav-color);
}

.header li {
    display: flex;
    align-content: center;
    align-items: center;
    margin: 10px 0;
}

.header li a {
    color: white;
    text-shadow: var(--text-shadow);
    display: block;
    /* padding: 20px 20px; */
    /* border-right: 1px solid var(--nav-color); */
    text-decoration: none;
    height: 100%;
}

/* .container>a {
    display: flex;
    align-content: center;
    align-items: center;
} */

.login-btn {
    display: flex;
    align-content: center;
    align-items: center;
    height: 100%;
}

.login-btn span {
    cursor: pointer;
    display: block;
    margin: auto;
}

.header .logo {
    display: flex;
    align-content: center;
    align-items: center;
    float: left;
    font-size: 1.2rem;
    font-weight: bold;
    text-decoration: none;
    color: white;
    text-shadow: var(--text-shadow);
    padding-top: 3px;
}

.header .logo span {
    font-size: 1.5rem;
    margin-right: 10px;
}

.active-link {
    font-weight: bold;
    color: var(--primary);
}

/* menu */

.header .menu {
    clear: both;
    max-height: 0;
    -webkit-transition: max-height .2s ease-out;
    transition: max-height .2s ease-out;
}

/* menu icon */

.header .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 20px 0;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.header .menu-icon .navicon {
    background: #fff;
    display: block;
    height: 2px;
    position: relative;
    -webkit-transition: background .2s ease-out;
    transition: background .2s ease-out;
    width: 18px;
}

.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
    background: #fff;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    -webkit-transition: all .2s ease-out;
    transition: all .2s ease-out;
    width: 100%;
}

.header .menu-icon .navicon:before {
    top: 5px;
}

.header .menu-icon .navicon:after {
    top: -5px;
}

/* menu btn */

.header .menu-btn {
    display: none;
}

.header .menu-btn:checked~.menu {
    max-height: 240px;
}

.header .menu-btn:checked~.menu-icon .navicon {
    background: transparent;
}

.header .menu-btn:checked~.menu-icon .navicon:before {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.header .menu-btn:checked~.menu-icon .navicon:after {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:after {
    top: 0;
}

@media (min-width: 768px) {
    .header li {
        float: left;
    }
    .login-btn span,
    .header li a {
        padding: 10px 20px;
    }
    .login-btn span {
        padding-right: 0;
    }
    .header .menu {
        clear: none;
        float: right;
        max-height: none;
    }
    .header .menu-icon {
        display: none;
    }
    .header .logo {
        height: 100%;
        padding-top: 0;
    }
    .header li a:hover,
    .header .menu-btn:hover,
    .header .logo:hover,
    .login-btn:hover {
        color: #ffcf33;
    }
}

/* section */

.section {
    overflow: hidden;
    margin: auto;
    max-width: 1400px;
}

.section a {
    position: relative;
    float: left;
    width: 100%;
}

.section a img {
    width: 100%;
    display: block;
}

.section a span {
    color: #fff;
    position: absolute;
    left: 5%;
    bottom: 5%;
    font-size: 2rem;
    text-shadow: 1px 1px 0 #000;
}

.section-split a span {
    display: none;
}

.section-split a:hover span {
    display: block;
}

@media (min-width: 768px) {
    .section-split a {
        width: 50%;
    }
}
.new-event-form,
.new-greeting-form {
    display: grid;
    grid-template-columns: 100px 1fr;
    grid-gap: 10px;
}

.guest-picker {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 5px;
}

.guest-input {
    display: grid;
    grid-template-columns: 5rem 1fr;
    grid-gap: 5px;
}

.icon-button {
    width: 2rem;
}

input,
.form select {
    padding: 5px;
}

/* Mobile */

@media (max-width: 768px) {
    .new-event-form {
        grid-template-columns: 1fr;
    }
    .guest-input {
        display: grid;
        grid-template-columns: 1fr;
    }
}
.greeting-tabs-container {
    display: flex;
    width: 100%;
    background-color: var(--primary-light);
}

.greeting-tab {
    color: white;
    padding: 10px;
    cursor: pointer;
}

.greeting-tab:hover {
    background-color: var(--primary);
}

.greeting-tab-pill {
    font-size: 0.8rem;
    border-radius: 50%;
    background-color: var(--secondary);
    width: 20px;
    height: 20px;
    display: inline-block;
    text-align: center;
    font-weight: bold;
    color: black;
}

.active-tab {
    background-color: var(--primary-dark);
}

.greeting-wrapper {
    border-bottom: 1px solid gray;
    display: grid;
    grid-template-columns: 75% 1fr;
    grid-gap: 10px;
}

.greeting-wrapper .buttons {
    display: flex;
}

.greeting-wrapper .buttons span {
    cursor: pointer;
    color: var(--primary-light);
    margin: 5px;
    -webkit-filter: drop-shadow(1px 1px 2px #666666);
            filter: drop-shadow(1px 1px 2px #666666);
}

.greeting-wrapper .readonly * {
    padding: 0;
    margin: 0;
}

/* Mobile */

@media (max-width: 768px) {
    .greeting-wrapper {
        grid-template-columns: 1fr;
    }
    .greeting-tabs-container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
    .greeting-tab {
        -webkit-filter: drop-shadow(1px 1px 2px #666666);
                filter: drop-shadow(1px 1px 2px #666666);
    }
}
.banner {
    position: absolute;
    width: 100%;
    bottom: 0;
    top: 60px;
    background-size: cover;
    min-height: -webkit-max-content;
    min-height: -moz-max-content;
    min-height: max-content;
}

.main-body {
    background-color: rgba(255, 255, 255, 0.7);
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
}

.home {
    background-image: url(/static/media/main_bg-min.74110a46.jpg);
}

.events {
    background-image: url(/static/media/events-bg.8826636b.jpg);
}

.about {
    background-image: url(/static/media/about-bg.2862b313.jpg);
}

.party {
    background-image: url(/static/media/party-bg.c31c6e18.jpg);
}

.greeting {
    background-image: url(/static/media/gr-bg.d934c050.jpg);
}

.details {
    background-image: url(/static/media/details-bg.af203670.jpg);
}

.hero {
    color: white;
    height: 150px;
    display: flex;
    align-items: center;
}

.hero .title {
    font-size: 4rem;
    font-weight: bold;
    text-shadow: 2px 2px 2px #333333;
}

.hero .description {
    font-size: 0.9rem;
    text-shadow: 1px 1px 1px #333333;
}

.btn {
    padding: 10px 20px;
    margin: 10px 0;
    border: none;
    background-color: blueviolet;
    color: white;
    border-radius: 20px;
    box-shadow: 3px 6px 3px -3px #666666;
}

.loader-container {
    margin: 10px 0;
}

h3 {
    color: blueviolet;
}

.user-events {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5px;
}

.user-events>div {
    padding: 3px;
    display: grid;
    grid-gap: 15px;
    grid-template-columns: 200px 150px repeat(3, minmax(-webkit-min-content, -webkit-max-content));
    grid-template-columns: 200px 150px repeat(3, minmax(min-content, max-content));
}

.event-details {
    display: grid;
    grid-template-columns: 100px 1fr;
    grid-gap: 10px;
}

.event-details ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.outer-wrapper {
    display: flex;
    justify-content: center;
    width: var(--mid-container-width);
}

@media (max-width: 768px) {
    .banner {
        top: 40px;
    }
    .user-events>div {
        margin-bottom: 5px;
        display: flex;
        flex-wrap: wrap;
    }
    .user-events>div span {
        margin-right: 10px;
        width: 30%;
    }
    .user-events>div span:first-child {
        font-weight: bold;
    }
}
.chat-screen-container {
    height: 300px;
    display: flex;
    flex-direction: column;
    border: 1px #2c303b solid;
}

.chat-container {
    display: flex;
    flex: 1 1;
}

.whos-online-list-container {
    width: 150px;
    flex: none;
    background-color: #2c303b;
    color: white;
    overflow-y: scroll;
}

.chat-list-container {
    background-color: white;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.message-list-container {
    overflow-y: scroll;
    flex: 1 1;
}

.message-list-container ul {
    list-style: none;
    padding: 0;
    margin: 10px
}

.message-list-container ul li {
    margin-top: 5px;
    margin-bottom: 5px;
}

.sender-username {
    font-weight: bold;
}

.message {
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

.send-container form {
    display: flex;
}

.send-container form input {
    color: inherit;
    background: none;
    outline: none;
    border-top: 1px #2c303b solid;
    flex: 1 1;
    font-size: 16px;
}

.whos-online-list {
    padding: 0;
    margin: 10px;
    font-size: 0.9rem;
}

.whos-online-list li {
    display: grid;
    grid-template-columns: 15px 1fr;
    grid-gap: 10px;
    margin: 10px 0;
}

.whos-online-list li div {
    display: flex;
    align-items: center;
}

.whos-online-pill {
    border-radius: 50%;
    width: 11px;
    height: 11px;
}

@media (max-width: 768px) {
    .whos-online-list-container {
        display: none;
    }
}
:root {
    /* colors */
    --page-bg: #f9fafe;
    --text-color: #3a416f;
    --primary-dark: #6d1b7b;
    --primary: #9c27b0;
    --primary-light: #af52bf;
    --secondary-dark: #b28900;
    --secondary: #ffc400;
    --secondary-light: #ffcf33;
    --nav-color: #017687;
    --nav-bg-color: #01768780;
    /* dimensions */
    --min-container-width: 95%;
    --mid-container-width: 75%;
    /* --max-container-width: 75%; */
    /* fonts */
    --body-font: 'Open Sans', sans-serif;
    --text-shadow: 1px 1px 1px #333333;
}

@font-face {
    font-family: 'Open Sans';
    src: local('OpenSans'), url(/static/media/OpenSans-Regular.d7d5d458.ttf) format('truetype');
}

@font-face {
    font-family: 'Open Sans';
    src: local('OpenSans'), url(/static/media/OpenSans-Bold.f5331cb6.ttf) format('truetype');
    font-weight: bold;
}

html {
    height: 100%
}

body,
html {
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    font-family: var(--body-font);
    background-color: #f9fafe;
    background-color: var(--page-bg);
}

button {
    font-family: 'Open Sans', sans-serif;
    font-family: var(--body-font);
    cursor: pointer;
}

.outer-wrapper {
    width: 100%;
}

.container {
    width: 75%;
    width: var(--mid-container-width);
}
/* Mobile */

@media (max-width: 768px) {
    .container {
        width: 95%;
        width: var(--min-container-width);
    }
}
